.animation-area{
    background:linear-gradient(to left,#552e66,#152c4e);
    width:100%;
    min-height:100vh;
}

.box-area{
    position:absolute;
    top: 0;
    left: 0;
    width:100%;
    height:100%;
    overflow:hidden;
}

.box-area li{
    position:absolute;
    display:block;
    list-style: none;
    background:rgba(250,250,255,0.2);
    -webkit-animation:animate 20s linear infinite;
            animation:animate 20s linear infinite;
    padding:4em;
    bottom:-200px;
}
.box-area li:nth-child(1){
    left: 86%;
    font-size: 10px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}
.box-area li:nth-child(2){
    left: 12%;
    font-size: calc(15px / 4);
    -webkit-animation-duration: 10s;
            animation-duration: 10s;
    -webkit-animation-delay:1.5s;
            animation-delay:1.5s;
}
.box-area li:nth-child(3){
    left: 70%;
    font-size: 12.5px;
    -webkit-animation-delay: 5.5s;
            animation-delay: 5.5s;
}
.box-area li:nth-child(4){
    left: 54%;
    font-size: calc(75px / 4);
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-animation-duration: 15s;
            animation-duration: 15s;
}

.box-area li:nth-child(5){
    left: 65%;
    font-size: 5x;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}

.box-area li:nth-child(6){
    left: 15%;
    font-size: calc(55px / 4);
    -webkit-animation-delay: 3.5s;
            animation-delay: 3.5s;
}
.box-area li:nth-child(7){
    left: 20%;
    font-size: calc(30px / 4);
    -webkit-animation-delay: 10s;
            animation-delay: 10s;
}


@-webkit-keyframes animate {
	0% {
		-webkit-transform: scale(0) translateY(0) rotate(0deg);
		        transform: scale(0) translateY(0) rotate(0deg);
		opacity: 1;
	}
	100% {
		-webkit-transform: scale(1.5) translateY(-800px) rotate(360deg);
		        transform: scale(1.5) translateY(-800px) rotate(360deg);
		opacity: 0;
	}
}


@keyframes animate {
	0% {
		-webkit-transform: scale(0) translateY(0) rotate(0deg);
		        transform: scale(0) translateY(0) rotate(0deg);
		opacity: 1;
	}
	100% {
		-webkit-transform: scale(1.5) translateY(-800px) rotate(360deg);
		        transform: scale(1.5) translateY(-800px) rotate(360deg);
		opacity: 0;
	}
}
@-webkit-keyframes opac {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}
@keyframes opac {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

.box-area li:hover{
    -webkit-animation: opac linear ;
            animation: opac linear ;
}

@media screen and (max-width:720px){
    .box-area li{
        bottom:-150px;
    }
}
.nav-container{
    display:-webkit-flex;
    display:flex;
    -webkit-align-items:center;
