.content-container{
    z-index: 30;
    position: relative;
    background-color:#191919;
    border-radius:2rem;
    min-height: 50vh;
    opacity: 1;
    transition: opacity 200ms;
}
.content-container.loading{
    opacity:0.5;
}
#lisType{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content:center;
            justify-content:center;
    padding:1rem;
}

#lisType select{
    outline:none;
    padding:0.6em 2em;
    font-size: clamp(1.2rem,2vw,1.5rem);
    text-align: center;
    background-color:#1e1824;
    color:white;
    border: 2px solid #610606;
}


.list-container{
    border-radius:1rem;
    border: 2px solid #610606;
    overflow: hidden;
}
.list-container div{
    display:-webkit-flex;
    display:flex;
    -webkit-align-items:center;
            align-items:center;
    -webkit-justify-content:flex-start;
            justify-content:flex-start;
    padding: 0.4rem;
    color: white;
    -webkit-animation: show 800ms linear ;
            animation: show 800ms linear ;
}
input[type="checkbox"]{
    margin: 0 1rem;
}
.list-container div:nth-child(odd){
    background-color: rgb(8, 10, 32);
}
.list-container div:nth-child(even){
    background-color: rgb(23, 179, 252);
}
@media screen and (min-width:720px){
    input[type="checkbox"]{
        margin: 0 2rem;
    }
}

@-webkit-keyframes show{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes show{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@-webkit-keyframes fade{
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}

@keyframes fade{
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}
.form-container{
    padding: 0.2rem 2rem;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction:column;
            flex-direction:column;
    -webkit-align-items:center;
            align-items:center;
    text-align: center;
    border: 2px solid #610606;

}

.form-container input[type="text"]{
    border:0;
    background:none;
    display:block;
    margin: 9px auto;
    text-align: center;
    border: 2px solid #3498db;
    padding: 14px 10px;
    width:200px;
    outline:none;
    color: white;
    border-radius:1.5rem;
    transition: 0.25s;
}

.form-container input[type="text"]:focus{
    width:240px;
    border-color: #2ecc71;
}

.form-container input[type="submit"]{
    border:0;
    background:none;
    display:block;
    margin: 20px auto;
    text-align: center;
    border: 2px solid #2ecc71;
    padding: 14px 40px;
    outline:none;
    color: white;
    border-radius:1.5rem;
    transition: 0.25s;
    cursor: pointer;
}

.form-container input[type="submit"]:hover{
    background-color:#2ecc71;
}

@media screen and (min-width:720px){
    .form-container input[type="text"]{
        width:240px;
    }
    .form-container input[type="text"]:focus{
        width:300px;
    }
    .form-container input[type="submit"]{
        padding: 14px 48px;
    }
}

@media screen and (min-width:960px){
    .form-container input[type="text"]{
        width:280px;
    }
    .form-container input[type="text"]:focus{
        width:360px;
    }
    .form-container input[type="submit"]{
        padding: 14px 56px;
    }
}
@media screen and (min-width:1080px){
    .form-container input[type="text"]{
        width:320px;
    }
    .form-container input[type="text"]:focus{
        width:400px;
    }
    .form-container input[type="submit"]{
        padding: 14px 60px;
    }
}
@-webkit-keyframes fade{
    0%{
        opacity:0;
    }
    0.001%{
        opacity:1;
    }
    100%{
        opacity:0;
    }
}
@keyframes fade{
    0%{
        opacity:0;
    }
    0.001%{
        opacity:1;
    }
    100%{
        opacity:0;
    }
}
.alert{
    position: fixed;
    border-radius:1.5rem;
    z-index: 31;
    width:100%;
    padding:1rem;
    top: 80px;
    opacity: 1;
    -webkit-animation:fade 5s linear forwards;
            animation:fade 5s linear forwards;
}
.alertContainer{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content:center;
            justify-content:center;
}
.alert.message{
    background-color:rgba(125, 172, 132, 0.918);
    color: rgb(0, 112, 0);
    border:2px solid rgb(0, 112, 0);
}
.alert.error{
    background-color:rgba(255, 226, 226, 0.89);
    color: rgb(150, 4, 4);
    border:2px solid rgb(150, 4, 4);
}
.alert.primary{
    background-color:rgba(149, 189, 216, 0.959);
    color: rgb(8, 16, 116);
    border:2px solid rgb(8, 16, 116);
}
.alert.warning{
    background-color:rgba(243, 237, 147, 0.89);
    color: rgb(255, 130, 14);
    border:2px solid rgb(255, 130, 14);
}

@media screen and (min-width:720px){
    .alert{
        width:70%;
    }
}


/* spinner */
.spinner{
    position: fixed;
    top: 50vh;
    left: 50vw;
    z-index: 31;
}
.footer-container{
    display:grid;
    -webkit-justify-content: center;
            justify-content: center;
    grid-template-columns: 1fr;
    padding: 1rem 0.5rem;
}
.social-icon,a{
    color:rgb(160, 135, 241);
}
svg{
    position: relative;
    top: 5px;
}
a:hover,svg:hover{
    color:rgb(59, 19, 241);
}
.tag{
    color: antiquewhite;
    text-align: center;
    font-size:1.3rem;
    font-family: Pacifico,Arial, Helvetica, sans-serif;
    padding-bottom: 3rem;
}
.social-item{
    font-size:1.3rem;
    display: grid;
    -webkit-justify-content:space-around;
            justify-content:space-around;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-row-gap: 1.5rem;
}
.social-item a{
    text-align: center;
}
.social-name{
    font-size: clamp(1.2rem,1.5vw,1.5rem);
    display:none;
}
@media screen and (min-width:720px){
    .footer-container{
        padding:1rem 5rem;
